import classNames from 'classnames';

const BALL = 'ball';

class Cannon extends skoash.Component {
  constructor() {
    super();

    this.state = {
      classes: {}
    }

    this.fire = this.fire.bind(this);
    this.reload = this.reload.bind(this);
  }

  bootstrap() {
    super.bootstrap();

    var list = this.refs.bin ? this.refs.bin.get(this.props.showNum + 1) : this.props.list;

    this.setState({
      list
    });
  }

  start() {
    super.start();
    this.next();
  }

  next() {
    var classes, list;
    classes = this.state.classes;
    list = this.state.list;
    if (this.props.reverseReload) {
      list = this.refs.bin.get(1).concat(list);
      list.pop();
    } else {
      list = list.concat(this.refs.bin.get(1));
      list.shift();
    }
    classes[this.state.list.length - 1] = 'LOADED';
    this.enabled = true;

    this.setState({
      classes,
      list
    });
  }

  fire() {
    this.setState({
      fire: true,
      reload: false
    }, () => {
      setTimeout(() => {
        this.reload();
      }, 500);
    });

    if (typeof this.props.onFire === 'function') {
      this.props.onFire.call(this);
    }

    this.next();
  }

  reload() {
    this.setState({
      fire: false,
      reload: true
    });

    if (typeof this.props.onReload === 'function') {
      this.props.onReload.call(this);
    }
  }

  getClassNames() {
    return classNames('cannon', {
      FIRE: this.state.fire,
      RELOAD: this.state.reload
    }, super.getClassNames());
  }

  getClass(key, li) {
    return classNames(
      'ammo',
      li.props.className,
      this.state.classes[key]
    );
  }

  renderBin() {
    if (!this.props.bin) return null;

    return (
      <this.props.bin.type
        {...this.props.bin.props}
        ref="bin"
      />
    );
  }

  renderAmmo() {
    var list = this.state.list || this.props.list;
    return list.map((li, key) => {
      var ref;
      ref = li.ref || li.props['data-ref'] || key;
      return (
        <li.type
          {...li.props}
          ref={ref}
          data-ref={ref}
          key={key}
          className={this.getClass(key, li)}
          onClick={this.fire}
        />
      );
    });
  }

  render() {
    return (
      <div className={this.getClassNames()}>
        {this.renderBin()}
        <div className="ammo-container" />
        {this.renderAmmo()}
      </div>
    );
  }
}

Cannon.defaultProps = _.defaults({
  showNum: 3,
  reverseReload: false,
  list: [
    <li></li>,
    <li></li>,
    <li></li>,
    <li></li>
  ]
}, skoash.Component.defaultProps);

export default Cannon;



